import React from 'react';
import { Jazzicon } from '@ukstv/jazzicon-react';
import styled from 'styled-components';
import {_numeral} from "../../lib/utils";

export const ModifiedJazzicon = styled(Jazzicon)`
    width: 32px;
    height: 32px;
    margin-right: 15px;
`
const Wrapper = styled.div`
  border: 1px solid rgba(255, 255, 255, 0.5);
  background-color: ${({ theme }) => theme.modalBG};
  padding: 10px 30px;
  border-radius: 18px;
  height: 50px;
  position: relative;
  transition: .3s ease all;
  margin-bottom: 0.625rem;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  
  &:hover {
    background-color: ${({ theme }) => theme.bg2};
    border-color: rgba(255, 255, 255, 0.8);
}
`

const Text = styled.span`
  color: white;
  font-size: 1rem;
  font-weight: 400;
`

const voteItem = props => {
    return (
        <Wrapper>
            <div className="d-flex align-items-center">
                <ModifiedJazzicon address={props.address}/>
                <Text>{props.address.slice(0, 6)}...{props.address.slice(-4)}</Text>
            </div>
            <Text className={'d-none d-lg-flex'}>{props.vote}</Text>
            <Text>{_numeral(props.score)} {props.symbol}</Text>
        </Wrapper>
    )
}

export default voteItem;